import Vue from 'vue'
import VueI18n from 'vue-i18n'
import messages from '@/lang/zh'
// import axios from 'axios'
import ElementLocale from 'element-ui/lib/locale'

// const langMessages = require(`@/lang/${localStorageLang}`).default // 

Vue.use(VueI18n)

export const i18n = new VueI18n({
  locale: process.env.VUE_APP_I18N_LOCALE || 'zh', // 
  fallbackLocale: process.env.VUE_APP_I18N_FALLBACK_LOCALE || 'zh',
  messages: {
    zh: messages,
  }, // 
})

function setI18nLanguage(lang) {
  i18n.locale = lang
  // axios.defaults.headers.common['Accept-Language'] = lang
  document.querySelector('html').setAttribute('lang', lang)
  return lang
}

ElementLocale.i18n((key, value) => i18n.t(key, value)) // element

const loadedLanguages = ['zh']

export function loadLanguageAsync(lang) {
  if (i18n.locale !== lang) {
    if (!loadedLanguages.includes(lang)) {
      return import(`@/lang/${lang}`).then(msgs => {
        i18n.setLocaleMessage(lang, msgs.default)
        loadedLanguages.push(lang)
        return setI18nLanguage(lang)
      })
    }
    return Promise.resolve(setI18nLanguage(lang))
  }
  return Promise.resolve(lang)
}

// 
const localStorageLang = localStorage.getItem('lang')

if (localStorageLang && localStorageLang !== i18n.locale) {
  loadLanguageAsync(localStorageLang)
}
